<template>
  <div class="product_list">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">home</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/platform' }"
          >平台服务</el-breadcrumb-item
        >
        <el-breadcrumb-item>细胞产品注册</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="product_main">
      <div class="steps">
        <div class="step" v-for="(item, index) of 6" :key="index">
          <img
            class="true_img"
            src="../../assets/img/done@2x.png"
            alt=""
            v-if="index < 1"
          />
          <div
            class="num"
            :class="[index == 1 ? 'shownum' : '', index < 1 ? 'lastnum' : '']"
            v-else
          >
            1
          </div>
          <div
            class="desc"
            :class="[index == 1 ? 'showdesc' : '', index < 1 ? 'hasdesc' : '']"
          >
            注册须知
          </div>
          <div class="line" v-if="index < 5"></div>
        </div>
      </div>
      <div class="info_row">
        <div class="info_name">项目：</div>
        <input class="info_input" type="text" placeholder="请输入" />
      </div>
      <div class="info_row">
        <div class="info_name">申请人：</div>
        <input class="info_input" type="text" placeholder="请输入" />
      </div>
      <div class="info_row">
        <div class="info_name">性别：</div>
        <div class="radio">
          <el-radio v-model="radio" label="1">男</el-radio>
          <el-radio v-model="radio" label="2">女</el-radio>
        </div>
      </div>
      <div class="info_row">
        <div class="info_name">电话：</div>
        <input
          class="info_input"
          type="text"
          placeholder="请输入13位手机号码"
        />
      </div>
      <div class="info_row">
        <div class="info_name">邮箱：</div>
        <input class="info_input" type="text" placeholder="请输入" />
      </div>
      <div class="info_row">
        <div class="info_name">公司/机构：</div>
        <div class="select">
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="info_row">
        <div class="info_name">细胞类型：</div>
        <div class="select">
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="info_row">
        <div class="info_name">邮箱：</div>
        <textarea class="area" name="" id="" cols="30" rows="10"></textarea>
      </div>
      <div class="admit" @click="toNext()">提交</div>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      check1: false,
      check2: false,
      check3: false,
      check4: false,
      radio: "1",
      options: [
        {
          value: "选项1",
          label: "黄金糕",
        },
        {
          value: "选项2",
          label: "双皮奶",
        },
        {
          value: "选项3",
          label: "蚵仔煎",
        },
        {
          value: "选项4",
          label: "龙须面",
        },
        {
          value: "选项5",
          label: "北京烤鸭",
        },
      ],
      value: "",
    };
  },
  methods: {
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    toNext() {
      this.$router.push({
        path: "/platform/stepthree",
        query: {},
      });
    },
  },
  created() {},
  components: {},
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.product_list {
  background: #f9f9f9;
}
.bottom {
  height: 24px;
}
.product_main {
  width: 1200px;
  background: #ffffff;
  margin: 0 auto;
  padding: 1px 0;
}
.steps {
  display: flex;
  justify-content: center;
  margin: 40px auto 40px;
  .step {
    display: flex;
    color: #aaaaaa;
    height: 32px;
    .num {
      width: 32px;
      height: 32px;
      border-radius: 16px;
      border: 1px solid #aaaaaa;
      text-align: center;
      line-height: 32px;
    }
    .true_img {
      width: 32px;
      height: 32px;
    }
    .desc {
      font-size: 16px;
      line-height: 32px;
      margin-left: 8px;
    }
    .line {
      width: 40px;
      height: 2px;
      background: #aaaaaa;
      margin: 0 16px;
      position: relative;
      top: 15px;
    }
    .shownum {
      background: #0695d4;
      color: #ffffff;
      border: 1px solid #0695d4;
    }
    .showdesc {
      font-weight: bold;
      color: rgba(0, 0, 0, 0.85);
    }
    .hasdesc {
      color: rgba(0, 0, 0, 0.65);
    }
  }
}
.info_row {
  display: flex;
  margin-top: 20px;
  .info_name {
    text-align: right;
    line-height: 40px;
    width: 364px;
    font-size: 14px;
    color: #787878;
  }
  .info_input {
    width: 468px;
    height: 40px;
    background: #ffffff;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: 2px;
    font-size: 14px;
    line-height: 40px;
    padding-left: 12px;
  }
  .radio {
    height: 40px;
    line-height: 40px;
  }
  .select {
    width: 468px;
    height: 40px;
  }
  .el-select {
    width: 100%;
  }
  .area {
    width: 468px;
    height: 88px;
    border: 1px solid #d9d9d9;
    padding: 5px 12px;
    line-height: 22px;
  }
}
.admit {
  width: 98px;
  height: 32px;
  background: #0695d4;
  border-radius: 2px;
  font-size: 14px;
  color: #ffffff;
  text-align: center;
  line-height: 32px;
  margin: 24px 369px 36px 733px;
  cursor: pointer;
}
</style>



